<template>
  <div id="app">
    <Header></Header>
    <keep-alive>
      <router-view></router-view>
    </keep-alive>
  </div>
</template>

<script>

  import Header from "@/components/Header";

  export default {
    name: 'App',
    components: {Header},
    mounted() {
      const that = this;
      window.onresize = function temp() {
        that.$store.dispatch('updateWindowParam', {width: window.screen.availWidth, height: window.screen.availHeight});
      };
    }
  }
</script>

<style>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    max-width: 1060px;
    margin: 0 auto;
  }


  @media (max-width: 700px) {
    .el-table {
      font-size: 12px;
    }

    .el-table td, .el-table th {
      padding: 5px 0;
    }

  }

  #app .el-table th.is-leaf {
    border-bottom: 0.1rem solid #ebeef5;
  }

</style>



