export function convertSec(sec) {
  const minutes = Math.round(sec / 60);
  return [Math.floor(minutes / 60), minutes % 60]
}

export function isBright(hex) {
  const c_r = parseInt(hex.substr(0, 2), 16);
  const c_g = parseInt(hex.substr(2, 2), 16);
  const c_b = parseInt(hex.substr(4, 2), 16);
  const brightness = ((c_r * 299) + (c_g * 587) + (c_b * 114)) / 1000;
  return brightness > 155;
}

export function heroIcon(heroName) {
  if (heroName === 'wreckingball')
    heroName = 'wrecking-ball';
  return `https://d1u1mce87gyfbn.cloudfront.net/hero/${heroName}/icon-portrait.png`
}

