import Vue from 'vue'
import Router from 'vue-router'
import Players from '@/components/Players'
import Player from '@/components/Player'

Vue.use(Router);

export default new Router({
  mode: 'history',
  base: '/owl/',
  routes: [
    {
      path: '/',
      name: 'Players',
      component: Players,
      meta: {
        title: "OWL Players Stats"
      }
    },
    {
      path: '/player/:id/:name',
      name: 'Player',
      component: Player,
      meta: {
        title: "OWL Players Stats"
      }
    }

  ]
})



