const axios = require("axios");

class PlayerBasic {
  constructor(player) {
    ['id', 'name', 'nationality', 'homeLocation', 'headshot', 'fullName', 'role', 'number'].forEach(key =>
      this[key] = player[key]
    );

    this.fullName = this.fullName ? this.fullName : `${player.givenName} ${player.familyName}`;
    this.nationality = player.nationality
    this.role = this.role === undefined && player.attributes ? player.attributes.role : this.role;
    this.number = this.number === undefined && player.attributes ? player.attributes.player_number : this.number;

  }
}

class Player {
  constructor(data) {
    this.basic = new PlayerBasic(data.player);
    this.team = new Team(data.team.data, {showPlayers: false});

    this.heroes = [];
    if (data.stats) {
      data.stats.heroes.forEach(hero => {
        let stats = hero.stats;
        stats.hero = hero.name;
        this.heroes.push(stats)
      });
      this.all = [];
      data.stats.all.type = "数据";
      this.all.push(data.stats.all);
      data.statRanks.type = "排名";
      this.all.push(data.statRanks);
    }


  }
}

class Team {
  constructor(team, {showPlayers = true} = {}) {
    ['id', 'name', 'abbreviatedName', 'logo', 'location', 'colors', 'matches', 'placement', 'records', 'schedule'].forEach(
      key => this[key] = team[key]);
    this.players = showPlayers ? team.players.map(x => new PlayerBasic(x)) : undefined;
  }
}

class Match {
  constructor(match, {showPlayers = true} = {}) {
    this.id = match.id;
    this.competitors = match.competitors.map(x => new Team(x, {showPlayers: showPlayers}))
    this.startDate = match.startDate;
    this.endDate = match.endDate;
    this.state = match.state;
    this.scores = match.scores;


  }
}

class OWL {
  constructor({inChina = false}) {
    this.api = axios.create({
      crossDomain: true,
      crossOrigin: true,
      baseURL: inChina === true ? 'https://api.overwatchleague.cn/' : 'https://api.overwatchleague.com/',
      params: {
        locale: inChina === true ? 'zh-cn' : 'en'
      }
    })
    ;
  }

  static get STAGE() {
    return {
      REGULAR: 'regular_season',
      POST: 'postseason'
    }
  }

  async getPlayer(id, {season = 2019, stage = OWL.STAGE.REGULAR} = {}) {
    return new Promise((resolve, reject) => {
      this.api.get(`/players/${id}`, {
        params: {
          season: season,
          stage_id: stage,
          expand: 'stats,stat.ranks,team,team.matches.recent,similarPlayers'
        }
      })
        .then(response => resolve(new Player(response.data.data)))
        .catch(err => reject(err))

    })

  }

  async getTeam(id) {
    return new Promise((resolve, reject) => {
      this.api.get(`/v2/teams/${id}`, {
        params: {
          expand: "schedule,matches.recent"
        }
      })
        .then(response => resolve(new Team(response.data.data)))
        .catch(err => reject(err))
    })
  }

  async allPlayers({season = 2019, stage = OWL.STAGE.REGULAR} = {}) {
    return new Promise((resolve, reject) => {
      this.api.get(`/stats/players`, {
        params: {
          season: season,
          stage_id: stage,
        }
      })
        .then(response => resolve(response.data.data))
        .catch(err => reject(err))
    })
  }

  async allTeams() {
    return new Promise((resolve, reject) => {
      this.api.get(`/teams`,)
        .then(response => {
          const teams = {};
          response.data.competitors.forEach(team => {
            teams[team.competitor.id] = team.competitor
          })
          resolve(teams)
        })
        .catch(err => reject(err))
    })
  }
}

export default OWL;




