<template>
  <div class="header">
    <div style="float: left">
      <img :src="logoUrl" style="height:40px">
    </div>
    <div class="top-right">
      <div class="top-search">
        <el-input placeholder="输入选手名称" prefix-icon="el-icon-search" v-model="search"></el-input>
      </div>
      <el-menu :default-active="activeIndex" mode="horizontal" @select="handleSelect">
        <el-menu-item index="1">选手</el-menu-item>
        <el-menu-item index="2">战队</el-menu-item>
        <el-menu-item index="3">比赛</el-menu-item>

      </el-menu>
    </div>
  </div>

</template>

<script>
  export default {
    name: "Header",
    data() {
      return {
        logoUrl: process.env.NODE_ENV === 'development' ? "/static/logo.png" : "/owl/static/logo.png"
      }
    },
    methods: {
      handleSelect(key) {
        if (key !== this.activeIndex)
          switch (key) {
            case "1":
              this.$router.push('/')

          }
      }
    },
    computed: {

      activeIndex() {

        return this.$store.getters.getCurrentTabIndex
      },
      search: {
        get() {
          return this.$store.getters.getCurrentSearch;
        },
        set(value) {
          this.$store.dispatch('updateSearch', value)
        }
      }
    }
  }
</script>

<style>
  .header .el-menu--horizontal > .el-menu-item {
    height: 42px;
    line-height: 40px;

  }

  .header .el-menu.el-menu--horizontal {
    border: none;
  }

  .header {
    height: 60px;
    box-sizing: border-box;
  }

  .header .top-right {
    float: right;
    display: flex;
    align-items: center;
  }

  .header .top-right .el-input {
    max-width: 9rem;
    margin: 0 10px;
  }

  .header .top-right .el-input__inner {
    border-radius: 20px;
    line-height: 30px;
    height: 30px;
    color: #757575;
    background-color: #f2f2f2;
  }

  .header .top-right .el-input__prefix {
    color: #757575;
  }

  .header .top-right .el-input.is-active .el-input__inner, .el-input__inner:focus {
    border-color: transparent;
  }

  .header .top-right .el-input__icon {
    line-height: 30px;

  }

  @media (max-width: 700px) {
    .header .top-right .el-menu-item, .el-input__inner {
      padding: 0 7px;
    }

    .header .top-right .el-input {
      max-width: 8rem;
    }
  }
</style>



